const min = function (number) {

    if (number.length > 2) {
        return min([number[0], min(number.slice(1))])

    } else {
        return result(number)
    }
}
const result = function (number) {
    return number[0] < number[1] ? number[0] : number[1]
}
min([1,4,66,75,76,97,100])

